import React from 'react'
import {
  StaticQuery,
  graphql,
} from 'gatsby'
import Image from 'gatsby-image'
import { Box } from 'grommet'
import { rhythm } from '../utils/typography'

function Bio() {
  return (
    <StaticQuery
      query={bioQuery}
      render={data => {
        const {
          author,
          social,
        } = data.site.siteMetadata
        return (
          <Box background="pink">
            <p>
              Written by{' '}
              <strong>{author}</strong>{' '}
              who lives and works in
              Berlin building useful
              things.
            </p>
          </Box>
        )
      }}
    />
  )
}

const bioQuery = graphql`
  query BioQuery {
    avatar: file(
      absolutePath: {
        regex: "/profile-pic.jpg/"
      }
    ) {
      childImageSharp {
        fixed(width: 50, height: 50) {
          ...GatsbyImageSharpFixed
        }
      }
    }
    site {
      siteMetadata {
        author
        social {
          twitter
        }
      }
    }
  }
`

export default Bio
